#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.App {
  text-align: center;
  display: flex;
  /*background-color: #A64253;*/
  justify-content: center;
  padding-right: 30px;
  margin-right: 30px;
  justify-content: space-around;
  /*flex-direction: column;*/
  /*border: 5px solid black;*/
  width: 900px;
  position: absolute;
  top: 50%;
  left: 30%;

}

.item{
  position: absolute;
  width: 450px;
  padding: 5px;
  display:flex;
  /*border: 3px solid black;*/
  border-radius: 5px;
  background-color: black;
  border: 1px solid black;


}



